'use client'

import { useQuery } from '@tanstack/react-query'
import { Card, Button, Dialog, TextField } from 'frosted-ui'
import { Users, Dumbbell, CheckCircle, UserPlus, Plus, UserCheck, Eye, Settings, MessageSquare, Clock, AlertTriangle } from 'lucide-react'
import Link from 'next/link'
import { useState } from 'react'
import { useWhop } from '~/components/whop-context'
import { dashboardStatsQuery, createPlanMutation, recentActivityQuery, clientsNeedingAttentionQuery, type RecentActivity, type ClientNeedingAttention } from '~/components/workouts/queries'
import { useMutation, useQueryClient } from '@tanstack/react-query'

export default function DashboardPage() {
  const { access, experience } = useWhop()
  const isAdmin = access.accessLevel === 'owner' || access.accessLevel === 'admin'
  const [newPlanOpen, setNewPlanOpen] = useState(false)
  const [newPlanTitle, setNewPlanTitle] = useState('')
  const qc = useQueryClient()
  
  if (!isAdmin) {
    return (
      <div className="p-6">
        <div className="text-sm opacity-70">You don't have access to this page.</div>
      </div>
    )
  }

  const { data: stats, isLoading, error } = useQuery(dashboardStatsQuery(experience.id))
  const { data: activityData, isLoading: isLoadingActivity } = useQuery(recentActivityQuery(experience.id))
  const { data: attentionData, isLoading: isLoadingAttention } = useQuery(clientsNeedingAttentionQuery(experience.id))

  const createPlan = useMutation({
    mutationFn: createPlanMutation(experience.id).mutationFn,
    onSuccess: () => {
      qc.invalidateQueries({ queryKey: ['dashboard-stats', experience.id] })
      qc.invalidateQueries({ queryKey: ['workout-plans', experience.id] })
      qc.invalidateQueries({ queryKey: ['recent-activity', experience.id] })
      setNewPlanOpen(false)
      setNewPlanTitle('')
    },
  })

  const formatActivity = (activity: RecentActivity) => {
    const timeAgo = new Date(activity.createdAt).toLocaleDateString()
    
    switch (activity.type) {
      case 'assignment':
        return {
          icon: UserCheck,
          description: `Assigned "${activity.planTitle}" to client`,
          time: timeAgo,
          color: 'text-blue-600'
        }
      case 'plan_creation':
        return {
          icon: Plus,
          description: `Created workout plan "${activity.planTitle}"`,
          time: timeAgo,
          color: 'text-green-600'
        }
      case 'message':
        return {
          icon: MessageSquare,
          description: `New message: ${activity.message?.substring(0, 50)}${activity.message && activity.message.length > 50 ? '...' : ''}`,
          time: timeAgo,
          color: 'text-purple-600'
        }
      default:
        return {
          icon: Clock,
          description: 'Unknown activity',
          time: timeAgo,
          color: 'text-gray-600'
        }
    }
  }

  const metrics = [
    {
      icon: Users,
      label: 'Total Clients',
      value: stats?.totalClients ?? 0,
      color: 'text-blue-600',
      bgColor: 'bg-blue-50 dark:bg-blue-950'
    },
    {
      icon: Dumbbell,
      label: 'Workout Plans',
      value: stats?.totalPlans ?? 0,
      color: 'text-green-600',
      bgColor: 'bg-green-50 dark:bg-green-950'
    },
    {
      icon: CheckCircle,
      label: 'Total Assignments',
      value: stats?.totalAssignments ?? 0,
      color: 'text-purple-600',
      bgColor: 'bg-purple-50 dark:bg-purple-950'
    },
    {
      icon: UserPlus,
      label: 'Recent Assignments',
      value: stats?.recentAssignments ?? 0,
      color: 'text-orange-600',
      bgColor: 'bg-orange-50 dark:bg-orange-950'
    }
  ]

  return (
    <div className="p-6">
      <div className="mb-6">
        <h1 className="text-2xl font-bold">Dashboard</h1>
      </div>

      {/* Clean Metrics Grid */}
      <div className="grid grid-cols-2 lg:grid-cols-4 gap-4 mb-6">
        {metrics.map((metric, index) => {
          const Icon = metric.icon
          return (
            <Card key={index}>
              <div className="p-4">
                <div className="flex justify-between items-start mb-2">
                  <p className="text-sm opacity-70">{metric.label}</p>
                  <Icon className={`w-4 h-4 ${metric.color}`} />
                </div>
                {isLoading ? (
                  <div className="h-6 w-12 bg-gray-200 dark:bg-gray-700 rounded animate-pulse"></div>
                ) : error ? (
                  <p className="text-sm text-red-600">Error</p>
                ) : (
                  <p className="text-2xl font-bold">{metric.value}</p>
                )}
              </div>
            </Card>
          )
        })}
      </div>

      {/* Quick Actions and Activity */}
      <div className="grid gap-4 grid-cols-1 md:grid-cols-2">
        <Card>
          <div className="p-4">
            <div className="flex justify-between items-start mb-3">
              <h2 className="text-lg font-semibold">Quick Actions</h2>
              <Settings className="w-4 h-4 text-gray-500" />
            </div>
            <div className="space-y-2">
              <Dialog.Root open={newPlanOpen} onOpenChange={setNewPlanOpen}>
                <Dialog.Trigger asChild>
                  <Button variant="outline" className="w-full flex items-center justify-start text-left">
                    <Plus className="w-4 h-4 mr-2" />
                    Create New Workout Plan
                  </Button>
                </Dialog.Trigger>
                <Dialog.Content>
                  <Dialog.Title>Create New Workout Plan</Dialog.Title>
                  <div className="mt-4">
                    <TextField.Input
                      placeholder="Plan title"
                      value={newPlanTitle}
                      onChange={(e: any) => setNewPlanTitle(e.target.value)}
                    />
                  </div>
                  <div className="mt-4 flex justify-end">
                    <Button
                      disabled={!newPlanTitle || createPlan.isPending}
                      onClick={() => createPlan.mutate({ title: newPlanTitle })}
                    >
                      {createPlan.isPending ? "Creating..." : "Create"}
                    </Button>
                  </div>
                </Dialog.Content>
              </Dialog.Root>

              <Link href={`/experiences/${experience.id}/clients`}>
                <Button variant="outline" className="w-full flex items-center justify-start text-left">
                  <UserCheck className="w-4 h-4 mr-2" />
                  Assign Workout to Client
                </Button>
              </Link>

              <Link href={`/experiences/${experience.id}/clients`}>
                <Button variant="outline" className="w-full flex items-center justify-start text-left">
                  <Users className="w-4 h-4 mr-2" />
                  View All Clients
                </Button>
              </Link>

              <Link href={`/experiences/${experience.id}/workouts`}>
                <Button variant="outline" className="w-full flex items-center justify-start text-left">
                  <Eye className="w-4 h-4 mr-2" />
                  View All Workouts
                </Button>
              </Link>
            </div>
          </div>
        </Card>

        <Card>
          <div className="p-4">
            <div className="flex justify-between items-start mb-3">
              <h2 className="text-lg font-semibold">Recent Activity</h2>
              <Clock className="w-4 h-4 text-gray-500" />
            </div>
            {isLoadingActivity ? (
              <div className="space-y-2">
                {[...Array(3)].map((_, i) => (
                  <div key={i} className="flex items-center space-x-3">
                    <div className="w-4 h-4 bg-gray-200 dark:bg-gray-700 rounded animate-pulse"></div>
                    <div className="flex-1">
                      <div className="h-3 bg-gray-200 dark:bg-gray-700 rounded animate-pulse mb-1"></div>
                      <div className="h-2 w-16 bg-gray-200 dark:bg-gray-700 rounded animate-pulse"></div>
                    </div>
                  </div>
                ))}
              </div>
            ) : activityData?.activities.length === 0 ? (
              <div className="text-sm opacity-70">No recent activity</div>
            ) : (
              <div className="space-y-3 max-h-64 overflow-y-auto">
                {activityData?.activities.slice(0, 5).map((activity) => {
                  const formatted = formatActivity(activity)
                  const Icon = formatted.icon
                  return (
                    <div key={activity.id} className="flex items-start space-x-3">
                      <Icon className={`w-4 h-4 mt-0.5 ${formatted.color}`} />
                      <div className="flex-1 min-w-0">
                        <p className="text-sm">{formatted.description}</p>
                        <p className="text-xs opacity-70">{formatted.time}</p>
                      </div>
                    </div>
                  )
                })}
              </div>
            )}
          </div>
        </Card>
      </div>

      {/* Client Needs Attention */}
      <div className="mt-6">
        <Card>
          <div className="p-4">
            <div className="flex justify-between items-start mb-3">
              <div className="flex items-center gap-2">
                <h2 className="text-lg font-semibold">Clients Needing Attention</h2>
                {attentionData?.totalCount && attentionData.totalCount > 0 && (
                  <div className="px-2 py-1 bg-orange-100 dark:bg-orange-900 text-orange-600 dark:text-orange-400 text-xs rounded-full">
                    {attentionData.totalCount}
                  </div>
                )}
              </div>
              <AlertTriangle className="w-4 h-4 text-orange-500" />
            </div>
            {isLoadingAttention ? (
              <div className="space-y-2">
                {[...Array(3)].map((_, i) => (
                  <div key={i} className="flex items-center justify-between p-2 border rounded">
                    <div className="flex-1">
                      <div className="h-4 bg-gray-200 dark:bg-gray-700 rounded animate-pulse mb-1"></div>
                      <div className="h-3 w-24 bg-gray-200 dark:bg-gray-700 rounded animate-pulse"></div>
                    </div>
                    <div className="h-8 w-16 bg-gray-200 dark:bg-gray-700 rounded animate-pulse"></div>
                  </div>
                ))}
              </div>
            ) : attentionData?.clientsNeedingAttention.length === 0 ? (
              <div className="text-sm opacity-70">All clients have workout assignments! ðŸŽ‰</div>
            ) : (
              <div className="space-y-2 max-h-64 overflow-y-auto">
                {attentionData?.clientsNeedingAttention.slice(0, 5).map((client) => (
                  <div key={client.id} className="flex items-center justify-between p-2 border rounded">
                    <div className="flex-1 min-w-0">
                      <div className="font-medium truncate">{client.name || client.username}</div>
                      <div className="text-xs opacity-70">@{client.username}</div>
                    </div>
                    <Link href={`/experiences/${experience.id}/clients`}>
                      <Button size="sm" variant="outline">
                        Assign
                      </Button>
                    </Link>
                  </div>
                ))}
                {attentionData && attentionData.totalCount > 5 && (
                  <div className="text-center pt-2">
                    <Link href={`/experiences/${experience.id}/clients`}>
                      <Button variant="soft" size="sm">
                        View All {attentionData.totalCount} Clients
                      </Button>
                    </Link>
                  </div>
                )}
              </div>
            )}
          </div>
        </Card>
      </div>
    </div>
  )
}


